@import url(https://fonts.googleapis.com/css?family=Bad+Script|Caveat);
@font-face{font-family:Led;src:url(/mk52/static/media/led.53c119ee.ttf)}body{margin:30px auto;padding:0;width:900px;font-family:sans-serif;background:url(/mk52/static/media/bg.df778c75.jpg)}.paper{background:url(/mk52/static/media/paper2.c13458de.png) repeat-y;width:758px;min-height:800px;box-sizing:border-box;margin:30px auto 30px 0px;padding:50px 99px;display:-webkit-flex;display:flex}.paper h2{margin:16px 0 0 48px;font-size:22px;line-height:32px}.paper ul{margin:0;padding-left:32px}.paper ul li{list-style-type:decimal}.paper table{width:100%;border-spacing:0;padding:1px}.paper table tr{height:30px}.paper table tr td,.paper table tr th{line-height:30px;font-size:18px}.paper table tr th{text-align:center}.paper table.bordered tr td:not(:first-child):before{content:' | ';float:left}.paper table.bordered tr:before{width:100px;display:block;position:absolute;content:'';height:1px;background-color:black}.paper table.bordered tr:first-child:before{height:0px}.App{text-align:center}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:80px}.App-header{background-color:#222;height:150px;padding:20px;color:white}.App-title{font-size:1.5em}.App-intro{font-size:large}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.noise{background-image:url(/mk52/static/media/noise.1cb2fcf3.png)}.content{width:100%;height:100%;-webkit-transform:rotate(1.85deg) scale(0.99465);transform:rotate(1.85deg) scale(0.99465);font-size:22px;line-height:32px;font-family:'Caveat', cursive}.content a,.content a:link,.content a:hover,.content a:visited{text-decoration:none}.content p{margin:0;margin-bottom:18px}.stickers{width:0px;margin-top:-16px;font-size:16px;font-family:'Bad Script', cursive}.stickers .sticker{height:100px;-webkit-transform-origin:50% 50%;transform-origin:50% 50%;transition:0.5s}.stickers .sticker div{position:absolute;width:300px;height:300px;box-sizing:border-box;padding:19px 19px 32px 32px}.stickers .sticker:hover,.stickers .sticker.active{background-color:rgba(0,0,0,0.5);height:200px;-webkit-transform:rotate(-6deg) translate(-5px, -15px);transform:rotate(-6deg) translate(-5px, -15px)}.stickers .sticker:hover:nth-child(2n),.stickers .sticker.active:nth-child(2n){-webkit-transform:rotate(6deg) translate(5px, -40px);transform:rotate(6deg) translate(5px, -40px)}.stickers .sticker a{display:block;height:100%;-webkit-transform:rotate(-3deg);transform:rotate(-3deg);text-align:center;line-height:21px;transition:0.3s;cursor:pointer}.stickers .sticker a h5{font-size:25px;line-height:25px;font-weight:normal;padding:0;margin:0}.stickers .sticker a.active img{opacity:1}.stickers .sticker a,.stickers .sticker a:hover,.stickers .sticker a:link,.stickers .sticker a:visited{text-decoration:none;color:black}.stickers .sticker img{opacity:0;transition:0.3s}@font-face{font-family:Led;src:url(/mk52/static/media/led.53c119ee.ttf)}code{font-family:Led;font-size:18px}


