ng:10px;box-shadow:1px 1px 10px rgba(0,0,0,0.75),-1px -1px 10px rgba(255,255,255,0.35)}div.mk52>.main>.left>.display>div>div{padding:30px 47px;background:#001416;width:7em;border-radius:10px;box-shadow:-1px -1px 10px 0px rgba(255,255,255,0.05);text-align:center;letter-spacing:2px;color:#9fd3ed;text-shadow:0px 0px 7px #9fd3ed;font-size:22px}div.mk52>.main>.left>.display>div .phantom{position:absolute;background:transparent;box-shadow:none;color:#9fd3ed10;text-shadow:0px 0px 5px #9fd3ed20}div.mk52>.main>.left>.switches{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex:1 0;flex:1 0;border-bottom-left-radius:0;border-bottom-right-radius:0;border-bottom:none}div.mk52>.main>.left>.switches .switch{-webkit-flex:1 1;flex:1 1;min-height:20px;min-width:50px}div.mk52>.main>.right{padding:19px 0 0 0}div.mk52>.main>.right>.keyboard{-webkit-flex:27 0;flex:27 0;margin:3px;border-radius:7px;box-shadow:inset 4px 4px 2px rgba(0,0,0,0.5),2px 0px 1px rgba(255,255,255,0.25);padding:8px;border-bottom-left-radius:0;border-bottom-right-radius:0}

